* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

nav {
  gap: 20px;
  margin: 0 20px 15px;
  display: flex;
}

a {
  padding: 15px 0;
  text-decoration: none;
  display: inline-block;
}

a.current {
  color: #3366e6;
}

ul {
  list-style: none;
}

li {
  max-width: 400px;
  font-size: 18px;
  font-weight: 700;
}

.country-list > li {
  align-items: center;
  gap: 8px;
  font-size: 24px;
  display: flex;
}

li:not(:last-child) {
  margin-bottom: 8px;
}

li > span {
  font-weight: 400;
}

